* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    max-width: 1230px;
    padding: 0 15px;
    margin: auto;
}

body {
    background: darkgreen;
}

.block {
    text-transform: capitalize;
    text-align: center;

    display: flex;
    flex-direction: column;
    row-gap: 50px;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 100vh;
    row-gap: 20px;
    background: #1e1d1d7f;
    transform: scale(1);
    transition: 0.3s;
}

.form-none {
    transform: scale(0);
}

input {
    padding: 10px;
    outline: none;
}

table {
    text-transform: uppercase;
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    border: 2px solid #000;
    border-left: none;
    border-right: none;
    color: aliceblue;
    font-weight: 200;
    padding: 10px 0;
    font-style: oblique;
}

td {
    text-transform: capitalize;
}

button {
    padding: 5px 7px;
    background: black;
    color: rgba(204, 208, 212, 0.833);
    border: none;
    border-radius: 5px;
    margin: 5px;
    text-transform: capitalize;
    cursor: pointer;
}

.editBtn {
    background: darkgoldenrod;
}

.deleteBtn {
    background: darkred;
}

h1,
h3 {
    color: rgba(247, 247, 248, 0.955);
    cursor: pointer;
}